@import url("https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@100..900&display=swap");
html,
body {
  font-family: "Libre Franklin", sans-serif;
  font-optical-sizing: auto;
  font-size: 14px;
}

#fundraiserlistMain input.button, #fundraiserlistMain input.button.primary, #fundraiserlistMain input.neonButton {
  margin-top: 5px;
  padding: 5px 15px;
  border: solid 2px transparent;
  font-size: 1em;
  background-color: #0479bb;
  color: #ffffff;
  cursor: pointer;
}
#fundraiserlistMain input.button:hover, #fundraiserlistMain input.button.primary:hover, #fundraiserlistMain input.neonButton:hover {
  color: #0479bb !important;
  border-color: #0479bb;
  background-color: #ffffff;
}
#fundraiserlistMain > form[name=pShoppingCartFormBean] fieldset {
  width: 100%;
  max-width: none !important;
  padding: 10px;
  box-sizing: border-box;
  min-width: auto !important;
}
#fundraiserlistMain > form[name=pShoppingCartFormBean] fieldset table tr {
  display: flex;
  flex-direction: column;
}
#fundraiserlistMain > form[name=pShoppingCartFormBean] input,
#fundraiserlistMain > form[name=pShoppingCartFormBean] select {
  min-width: initial;
  height: 50px;
  border: 1px solid gray;
  border-radius: 8px;
}
#fundraiserlistMain > div {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}
#fundraiserlistMain > div .fundraiser {
  outline: 1px solid #ccc;
  padding: 8px;
  margin: 0;
  width: 100%;
  box-sizing: border-box;
  float: none;
}
#fundraiserlistMain > div .fundraiser tr:nth-child(2) td:first-of-type {
  width: 100px !important;
  height: 100px;
  position: relative;
}
#fundraiserlistMain > div .fundraiser:last-of-type {
  width: 100%;
  flex: auto;
}

div#fundraiserPhoto,
div#fundraiserPhoto > div {
  position: absolute;
  top: 0;
  left: 0;
  height: 100% !important;
  width: 100% !important;
}
div#fundraiserPhoto img,
div#fundraiserPhoto > div img {
  height: 100% !important;
  width: 100% !important;
  max-height: none !important;
  max-width: none !important;
  object-fit: cover;
  object-position: top;
}
div#fundraiserPhoto img[src="/np/images/defaultContact.jpeg"],
div#fundraiserPhoto > div img[src="/np/images/defaultContact.jpeg"] {
  padding-left: 100px;
  background-size: cover;
}

div#fundraiser-pagination .pagingContainer .form-group {
  white-space: normal;
  height: auto;
}

#sfp-sidebar a.sfp-button {
  background-color: #a6c75f;
}
#sfp-sidebar #sfp-progress-bar .sfp-progess-bar-container {
  position: relative;
}
#sfp-sidebar #sfp-progress-bar .sfp-progess-bar-container .sfp-progress-percentage {
  position: absolute;
  left: 0;
  width: 100%;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
}
#sfp-sidebar #sfp-progress-bar .sfp-progess-bar-container .sfp-progess-bar {
  top: 0;
  height: 100%;
}

@media screen and (min-width: 600px) {
  #fundraiserlistMain > div .fundraiser {
    flex: 0 1 calc(50% - 0.5rem);
  }
}
@media screen and (min-width: 900px) {
  #fundraiserlistMain > form[name=pShoppingCartFormBean] fieldset table tr {
    flex-direction: row;
    gap: 1rem;
  }
  #fundraiserlistMain > div .fundraiser tr:nth-child(2) td:first-of-type {
    width: 200px !important;
    height: 200px;
  }
  div#fundraiserPhoto > div img[src="/np/images/defaultContact.jpeg"] {
    padding-left: 200px;
    background-size: cover;
  }
  .content {
    display: flex;
  }
  .content #sfp-wrapper {
    flex: 0 0 60%;
  }
  .content #sfp-sidebar {
    margin-top: 10px;
    flex-grow: 1;
  }
}
footer.site-footer .social-icons .circle-icon-link {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border: 1px solid currentColor;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 -7px;
}
footer.site-footer .social-icons .circle-icon-link svg {
  width: 20px;
  height: 20px;
  fill: white;
}
footer.site-footer .social-icons .circle-icon-link:hover {
  background-color: white;
}
footer.site-footer .social-icons .circle-icon-link:hover svg {
  fill: #0479bb;
}
@media (min-width: 600px) {
  footer.site-footer .site-footer__grid {
    display: flex;
    gap: unset;
    justify-content: space-between;
    max-width: 1000px;
    margin: 0 auto;
  }
  footer.site-footer .site-footer__grid > div {
    width: calc(33% - 5rem);
    box-sizing: border-box;
  }
  footer.site-footer .site-footer__grid > div p {
    color: #fff;
  }
}

form select,
form input {
  padding: 10px;
  min-height: 30px;
  margin: 0;
  box-sizing: border-box;
  margin-top: 10px;
  /* Overrides  and Exceptions */
}
@media (min-width: 600px) {
  form select,
  form input {
    min-width: 600px;
  }
}
form select[type=text i],
form input[type=text i] {
  border-radius: 5px;
  display: flex;
  height: 3.75rem;
  width: 100%;
  border: 1px solid #c3c3c3;
  background: #f9f9f9;
  font-weight: 600;
  padding: 0 1em;
}
form select[type=file i], form select[type=image i], form select[type=checkbox i], form select[type=radio],
form input[type=file i],
form input[type=image i],
form input[type=checkbox i],
form input[type=radio] {
  width: auto;
  min-width: auto;
  min-height: auto;
}
form select[type=radio],
form input[type=radio] {
  width: 20px;
  height: 20px;
  transform: translateY(25%);
  margin-right: 10px;
}
form select[name*=zip],
form input[name*=zip] {
  min-width: auto;
  display: inline;
  width: auto;
}
form textarea {
  width: 100%;
  padding: 10px;
  min-height: 100px;
  font-size: 22px;
  margin: 0;
  box-sizing: border-box;
  margin-top: 10px;
}
@media (min-width: 600px) {
  form textarea {
    min-width: 600px;
  }
}
form .neoncrm-form-buttons {
  display: flex;
  justify-content: center;
}
form .neoncrm-form-buttons .neoncrm-button.neoncrm-submit {
  background-color: #0479bb;
  font-size: 22px;
  max-width: 300px;
  width: 300px;
  text-transform: uppercase;
  margin-bottom: 40px;
  color: white;
  min-width: initial;
}

/* This CSS was stored in Neon */
hr {
  width: 100%;
  height: 4px;
  margin-left: auto;
  margin-right: auto;
  background-color: #0479bb;
  border: 1 none;
  margin-top: 30px;
  margin-bottom: 30px;
}

/* customizing general donation form */
.neoncrm-radio-group.neoncrm-donation-levels label {
  font-size: 24px;
  line-height: 48px;
  display: inline-block;
  padding: 5px 15px;
  color: #fff;
  background-color: #0479bb;
  margin: 10px;
  min-width: 48px;
  min-height: 48px;
  border: solid 1px #ddd;
  border-radius: 4px;
  text-align: center;
}

.neoncrm-radio-group.neoncrm-donation-levels label:hover {
  background-color: #a6c75f;
  color: #fff;
}

.neoncrm-radio-group.neoncrm-donation-levels input[type=radio] {
  display: none;
}

.neoncrm-radio-group.neoncrm-donation-levels input:checked + label {
  background-color: #895ad2;
  color: #fff;
}

.neoncrm-donation-levels .neoncrm-radio-button {
  display: inline-block;
}

@media (max-width: 600px) {
  .neoncrm-donation-levels .neoncrm-radio-button {
    display: block;
  }
  .neoncrm-radio-group.neoncrm-donation-levels label {
    display: block;
  }
}
@media (min-width: 768px) {
  #form-wrapper {
    margin: 30px 0 30px 0;
    border: none;
    box-shadow: none;
  }
}
/* modifications to iFrames */
.iframe-container {
  overflow: hidden;
  padding-top: 56.25%;
  position: relative;
}

.iframe-container iframe {
  border: 0;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

/* 4x3 Aspect Ratio */
.iframe-container-4x3 {
  padding-top: 75%;
}

/* modifications to events */
.neoncrm-event-date {
  font-size: 16pt;
  text-align: center;
}

.neoncrm-event-name {
  font-size: 22pt;
  text-align: center;
}

/*modifications to alternative web template*/
.neoncrm-page-title {
  color: #0479bb;
  display: none;
}

/*modification to p2p */
#fundraiserlistMain .neon2Header {
  display: none;
}

.sfp-section-header {
  color: #0479bb;
  font-family: Roboto, sans-serif;
  font-size: 1.5ems;
}

#sfp-progress-bar .sfp-progess-bar {
  background-color: #a6c75f;
}

/* Social Fundraising Footer changes */
#footer-left p {
  font-size: 12px;
  line-height: 18px;
  color: white;
}

#footer-left h3 {
  font-family: Roboto, sans-serif;
  text-transform: capitalize;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.1;
  color: white;
}

#footer-left {
  width: 100%;
  padding-left: 5%;
}

#footer {
  padding: 10px;
}

#fundraiserlistMain input.button:hover,
#fundraiserlistMain input.button.primary:hover,
#fundraiserlistMain input.neonButton:hover {
  color: #555 !important;
}

/* Updates default P2P image */
[src="/np/images/defaultContact.jpeg"] {
  display: block;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  background: url(https://secure.lcfamerica.org/np/viewDocument?orgId=lcfamerica&id=4028e48c6a4a054e016a4b91816c0019) no-repeat;
  width: 100px;
  height: 100px;
  padding-left: 100px;
}

/* Customize Event Description page */
.neoncrm-page-subtitle {
  display: none;
}

.background-blue {
  background-color: #0479bb;
}

.background-green {
  background-color: #a6c75f;
}

body {
  margin: 0%;
  background-image: none;
}
body.background-fixed {
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
}
body.background-no-repeat {
  background-repeat: no-repeat;
}

.background-image-bubble-ribbons {
  background-image: url("https://d2r0txsugik6oi.cloudfront.net/neon/resource/lcfamerica/templateImage/footerBackgroundImage_61.png?forwardedFromSecureDomain=1&r=25008611172196628");
}

.background-image-blue-swirls {
  background-image: url("https://d2r0txsugik6oi.cloudfront.net/neon/resource/lcfamerica/templateImage/footerBackgroundImage_55.png");
}

.background-image-candles {
  background-image: url("https://d2r0txsugik6oi.cloudfront.net/neon/resource/lcfamerica/images/BackgroundImage_candles.png");
  background-position: bottom left;
}

.background-image-chiclets {
  background-image: url("https://d2r0txsugik6oi.cloudfront.net/neon/resource/lcfamerica/images/Blue-chicklet-full-screen.png");
}

.background-image-sky {
  background-image: url("https://d2r0txsugik6oi.cloudfront.net/neon/resource/lcfamerica/images/BackgroundImage_sky.jpg");
}

.text-white {
  color: #fff;
}
.text-white * {
  color: inherit;
}

header.lcfa-header {
  display: flex;
  justify-content: center;
  padding: 20px;
}

.wrapper {
  max-width: initial;
}

img.logo-image {
  max-height: 120px;
}

.content {
  outline: 1px solid white;
  background-color: #fff;
  margin: 40px 1% 80px;
  padding: 40px 40px 80px;
  max-width: 1000px;
  border-radius: 20px;
  box-sizing: border-box;
}
.content h1 {
  margin-top: 0;
}
@media (min-width: 1200px) {
  .content {
    margin-left: auto;
    margin-right: auto;
  }
}

footer.lcfa-footer {
  padding: 40px 40px 60px;
  display: flex;
}
footer.lcfa-footer * {
  margin: 0;
}
footer.lcfa-footer .column {
  outline: 1px solid white;
}